body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}
* {
    box-sizing: border-box;
}
html,
body {
    width: 100%;
    height: 100%;
}
ul,
li,
p {
    list-style: none;
    padding: 0;
    margin: 0;
}
.clearfix {
    *zoom: 1;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
    line-height: 0;
}

.clearfix:after {
    clear: both;
}
.list {
    background-color: lightblue;
    white-space: nowrap;
}
.list-item {
    display: inline-block;
}
.list-item.more {
    position: absolute;
    right: 0;
}
.list-item p {
    display: inline-block;
    padding-left: 20px;
    padding-right: 20px;
}
.more-content {
    position: absolute;
    right: 0;
    overflow-y: auto;
}

